.clipboard-button {
  position: relative;
  overflow: hidden;
}
.clipboard-button::before {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  content: 'Copied!';
  font-family: 'Source Code Pro';
  text-align: center;
  color: #fff;
  font-weight: 600;
  line-height: 32px;
  background: var(--color-primary);
  -webkit-transform: scaleX(0);
      -ms-transform: scaleX(0);
          transform: scaleX(0);
  -webkit-transform-origin: top right;
      -ms-transform-origin: top right;
          transform-origin: top right;
  -webkit-transition: -webkit-transform 405ms var(--good-easing);
  transition: -webkit-transform 405ms var(--good-easing);
  -o-transition: transform 405ms var(--good-easing);
  transition: transform 405ms var(--good-easing);
  transition: transform 405ms var(--good-easing), -webkit-transform 405ms var(--good-easing);
}

.clipboard-button--active::before {
  -webkit-transform: scaleX(1);
      -ms-transform: scaleX(1);
          transform: scaleX(1);
  -webkit-transform-origin: top left;
      -ms-transform-origin: top left;
          transform-origin: top left;
}



